.header{
  position: relative;
  &__wrapper{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
  }
  &__inner{
    @include flex($sb);
    align-items: center;
    
    margin: 2rem 0;
  }
  
  &__lang{
    margin-left: 3rem;
    span{
      color: $secondary;
    }
  }
}
.menu{
  margin-left: auto;

  &__btn{
    display: none;
  }
  &__list{
    display: flex;
  }
  &__item{
    margin-left: 3rem;
  }
}
